.modal{
	position: fixed;
	top: 0;
	right: 0;
	left: 0;
	bottom: 0;
	background-color: rgba(0,0,0,.5);
	display: flex;
	justify-content: center;
	-ms-align-items: center;
	align-items: center;
	padding-top: -5rem;
}

.modal-body{
	padding: 2rem;
	width: 400px;
	border-radius: 5px;
	background-color: #fff;
	height: 200px;
}